import React, { Component } from 'react';
import {Link} from 'react-router';
import './Charge.css'

var MChargeStyle = React.createClass({

        propTypes: {
            //
            title: React.PropTypes.string.isRequired,
            //
            titleRight: React.PropTypes.string.isRequired,
            //
            siteName: React.PropTypes.string.isRequired,
            //
            pileNumber: React.PropTypes.string.isRequired,
            //
            pileStyle: React.PropTypes.string.isRequired,
            //
            iStandard: React.PropTypes.string.isRequired,
            //
            current: React.PropTypes.string.isRequired,
            //
            voltage: React.PropTypes.string.isRequired,
            //
            power: React.PropTypes.string.isRequired,
            //
            cost: React.PropTypes.string.isRequired,
            //
            callNumber: React.PropTypes.string.isRequired
        },

        render: function () {
            return <div>
            <div className="title_cell">
                <span className="title"> {this.props.title} </span>
                <span className="btn_close fr" onclick={ window.close() }> {this.props.titleRight} </span>
            </div>

            <div className="background_while hg25 f_s_14 bb_d8 p20">
                <div className="f_w_700 t_a_c mb_08">{this.props.siteName}</div>
                <div className="m00_a">
                    <div className="color_33"><p></p><span className="color_99">{this.props.pileNumber}</span></div>
                    <div className="color_33"><p></p><span className="color_99">{this.props.pileStyle}</span></div>
                    <div className="color_33"><p></p><span className="color_99">{this.props.iStandard}</span></div>
                    <div className="color_33"><p></p><span className="color_99">{this.props.current}</span></div>
                    <div className="color_33"><p></p><span className="color_99">{this.props.voltage}</span></div>
                    <div className="color_33"><p></p><span className="color_99">{this.props.power}</span></div>
                </div>

            </div>
                <div className="background_while hg25 f_s_14 color_99 bb_d8 mt_10">
                    <div className="fl hg25">
                        <div className="money_icon fl"></div>
                        <p className="d_i_b f_s_14 color_45"></p>
                    </div>
                    <div className="w_55 d_i_b l_h_25 m_l_7p">{this.props.cost}</div>
                </div>
                <div className="background_while hg25 bb_d8 mb_40 f_s_12">{this.props.callNumber}
                <a className="phone_btn fr m_r_15" href={this.props.callNumber}></a><i className="sg"></i>
                </div>
                <div className="charging_start_btn t_a_c"></div>
            </div>;
        }
    }
);

class ChargeStyle extends Component {
    componentDidMount() {
        fetch("www.baidu.com").then(res => {
            this.setState({data : res})
        });
    }

    render() {
        return (<div>
            <Link>
                <MChargeStyle title="" titleRight="" siteName="863" pileNumber="365106011"
                              pileStyle="" iStandard="" current="16A" voltage="220V"
                              power="3.5KW" cost="00:00 - 24:00 2.0/" callNumber="400-030-7759"/>
            </Link>
        </div>);
    }
}

export default ChargeStyle;


