import React from 'react'
import { Link } from 'react-router'
import 'antd-mobile/lib/list/style'

// import { List } from 'antd-mobile';

import List from 'antd-mobile/lib/list'
// import {Menu} from './Menu'
import 'antd-mobile/lib/list/style'
import 'antd-mobile/dist/antd-mobile.css'
const Item = List.Item;
const Brief = Item.Brief;


export default class ListExample extends React.Component {
    state = {
        disabled: false,
    }
    render() {
        return (<div>
            <List renderHeader={() => ''} className="my-list">
                <Item extra={''}></Item>
            </List>
            <List renderHeader={() => ''} className="my-list">
                <Item arrow="horizontal" multipleLine>
                     <Brief></Brief>
                </Item>
                <Item
                    arrow="horizontal"
                    multipleLine
                    onClick={() => {}}
                    platform="android"
                >
                    ListItem Android<Brief>Clickmaterial</Brief>
                </Item>
                <Item
                    arrow="horizontal"
                    thumb="https://zos.alipayobjects.com/rmsportal/dNuvNrtqUztHCwM.png"
                    multipleLine
                    onClick={() => {}}
                >
                     <Brief></Brief>
                </Item>
            </List>
            <List renderHeader={() => ' /  / '} className="my-list">
                <Item></Item>
                <Item arrow="horizontal" onClick={() => {}}></Item>
                <Item extra="" arrow="horizontal" onClick={() => {}}></Item>
                <Item extra="10:30" align="top" thumb="https://zos.alipayobjects.com/rmsportal/dNuvNrtqUztHCwM.png" multipleLine>
                     <Brief></Brief>
                </Item>
            </List>
            <List renderHeader={() => ''} className="my-list">
                <Item multipleLine extra="">
                     <Brief></Brief>
                </Item>
            </List>
            <List renderHeader={() => ''}>
                <Item
                    thumb="https://zos.alipayobjects.com/rmsportal/dNuvNrtqUztHCwM.png"
                    arrow="horizontal"
                    onClick={() => {}}
                ></Item>
                <Item thumb="https://zos.alipayobjects.com/rmsportal/UmbJMbWOejVOpxe.png" arrow="horizontal"></Item>
            </List>
            <List renderHeader={() => ''} className="my-list">
                <Item data-seed="logId"></Item>
                <Item wrap></Item>
                <Item extra="" multipleLine align="top" wrap>
                    
                </Item>
                <Item extra="" arrow="empty" className="spe" wrap>
                    
                </Item>
            </List>
            <List renderHeader={() => ''} className="my-list">
                <Item disabled={this.state.disabled} extra="" onClick={() => { console.log('click', this.state.disabled); this.setState({ disabled: true }); }}></Item>
                <Item>
                    <select defaultValue="1">
                        <option value="1"> html select</option>
                        <option value="2" disabled></option>
                        <option value="3">3</option>
                    </select>
                </Item>
            </List>
        </div>);
    }
}

